import React from "react";
import { Modal, Button } from "antd";

import { ThinText } from "../../../../../common/StyledComponents";

const ConfirmCancelModal = ({ visible, setVisible, cancelTask }) => {
  const onCancelTask = () => {
    cancelTask();
    setVisible(false);
  };
  return (
    <Modal
      visible={visible}
      onOk={() => setVisible(false)}
      onCancel={() => setVisible(false)}
      footer={[
        <Button key="yes" onClick={onCancelTask}>
          
        </Button>,
        <Button key="no" type="primary" onClick={() => setVisible(false)}>
          
        </Button>
      ]}
    >
      <ThinText>  ?</ThinText>
    </Modal>
  );
};

export default ConfirmCancelModal;
